import Web3 from 'web3/dist/web3.min.js'
import store from '@/store';
import BAFCAbi from '@/utils/abi/BAFC';

let bafc;

class BAFCContractService {
  async subscribeToTokenMinted(onData, onError) {
    const web3 = new Web3(store.state.provider);
    const options = {
      filter: {
          value: [],
      },
      fromBlock: await web3.eth.getBlockNumber()
    };

    this._getContract().events.TokenMinted(options)
      .on('data', onData)
      .on('error', onError)
  }

  _getContract() {
    if (bafc) {
      return bafc;
    }

    const web3 = new Web3(store.state.provider);
    bafc = new web3.eth.Contract(BAFCAbi.abi, '0x9Fc9c438012c6B7F6f970Bf0CD057429D6a6DE6C');
    return bafc;
  }

  async hasClaimedFreeMint() {
    try {
      return await this._getContract().methods.claimedFreeMint(store.state.account).call();
    } catch (error) {
      console.error(error);
    }
  }

  async isPaused() {
    try {
      return await this._getContract().methods.paused().call();
    } catch (error) {
      console.error(error);
    }
  }

  async mint(amount) {
    try {
      const price = await this._getContract().methods.cost(amount).call({ from: store.state.account });
      const transaction = bafc.methods.mint(amount);
      await transaction.send({ from: store.state.account, value: price });
    } catch (error) {
      console.error(error);
    }
  }
}

export default new BAFCContractService();