import { createStore } from 'vuex';
import Web3Modal, { getInjectedProviderName  } from "web3modal";
import { getAccounts, getChainId } from '@/utils/web3';

const ETH_MAIN_NET_ID = 1; // 4 is Rinkeby. Change to 1

const web3Modal = new Web3Modal({
  network: "mainnet",
  // cacheProvider: true,
});

const store = createStore({
  state: {
    provider: null,
    account: null,
    chainId: null,
  },
  mutations: {
    setProvider(state, provider) {
      state.provider = provider;
      console.debug('setProvider', state.provider);
    },
    setAccount(state, accounts) {
      state.account = accounts.length > 0 ? accounts[0] : null;
      localStorage.setItem("account", state.account);
      console.debug("setAccount", state.account);
    },
    setChainId(state, chainId) {
      state.chainId = parseInt(chainId);
      console.debug("setChainId", state.chainId);
    },
  },
  actions: {
    init: async ({ dispatch }) => {
      console.debug('init');
      dispatch("connectWallet");
    },

    async connectWallet({ dispatch }, payload = { organic: false }) {
      console.debug('connectWallet');
      let account = localStorage.getItem("account");
      account = account ? eval(account) : null;
      if (getInjectedProviderName() && (account || payload.organic)) {
        try {
          const provider = await web3Modal.connect();
          dispatch('loadProvider', provider);
        } catch (error) {
          // Clear cached account if user reject the wallet connection request
          if (account) {
            store.commit('setAccount', []);
          }
          console.error(error)
        }
      }
    },

    async loadProvider({ commit, state }, provider) {
      console.debug('loadProvider');
      if (state.provider) {
        removeListeners(state.provider);
      }
      addListeners(provider);
      commit('setProvider', provider);

      try {
        const accounts = await getAccounts(provider);
        commit('setAccount', accounts);
  
        const chainId = await getChainId(provider);
        commit('setChainId', chainId);
      } catch (error) {
        console.error(error);
      }
    },
  },
  getters: {
    hasInjectedProvider() {
      if (!getInjectedProviderName()) {
        return false;
      }
      return true;
    },
    isWalletConnected(state) {
      if (!state.account) {
        return false;
      }
      return true;
    },
    isChainSupported(state) {
      return state.chainId === ETH_MAIN_NET_ID;
    }
  }
});

export default store;

function addListeners(provider) {
  provider.on("accountsChanged", onAccountsChanged);
  provider.on("chainChanged", onChainChanged);
}

function removeListeners(provider) {
  provider.removeListener("accountsChanged", onAccountsChanged);
  provider.removeListener("chainChanged", onChainChanged);
}

function onAccountsChanged(accounts) {
  store.commit('setAccount', accounts);
}

function onChainChanged(chainId) {
  store.commit('setChainId', chainId);
}

store.dispatch("init");
